<template>
  <div class="v-picker-column" :class="column.className">
    <ul class="v-picker-items" :style="style">
      <li
        class="v-picker-item"
        :class="{
          'v-picker-item-selected': selectedIndex === index,
          'v-picker-item-disabled': value.disabled
        }"
        v-for="(value, index) in column.values"
        :key="index"
      >
        {{ getValue(value) }}
      </li>
    </ul>
    <div class="v-picker-touch-overlay" :data-index="columnIndex"></div>
  </div>
</template>

<script>
export default {
  name: 'PickerColumn',
  props: {
    column: {
      type: Object,
      default: () => {}
    },
    columnIndex: Number,
    itemHeight: {
      type: Number,
      default: 40
    }
  },
  computed: {
    style() {
      return {
        transitionDuration: `${this.duration}ms`,
        transform: `translate3d(0, ${this.translate}px, 0)`
      }
    }
  },
  data() {
    return {
      vy: 0,
      duration: 0,
      translate: 0,
      selectedIndex: this.column.selectedIndex || 0,
      getValue: this.$parent.getValue
    }
  },
  created() {
    this.select(this.selectedIndex, 0)
  },
  methods: {
    select(index, duration, emit = true) {
      if (typeof index !== 'number') return
      const selectedIndex = this.selectedIndex
      const maxIndex = Math.max(this.column.values.length - 1, 0)

      index = Math.min(Math.max(index, 0), maxIndex)

      if (
        this.column.values[index] !== undefined &&
        this.column.values[index].disabled
      ) {
        index = selectedIndex
      }

      this.duration = duration
      this.translate = -(index - 2) * this.itemHeight
      this.selectedIndex = index

      if (index !== selectedIndex && emit) this.$emit('change')
    }
  }
}
</script>

<style lang="postcss">
.v-picker-column {
  position: relative;
  flex: 1;
  height: 100%;
  font-size: 16px;
  color: var(--textRegular);
  text-align: center;
  overflow: hidden;
}
.v-picker-items {
  margin: 0;
  padding: 0;
  list-style: none;
  transition-property: transform;
  transition-timing-function: ease;
}
.v-picker-item {
  padding: 0 6px;
  height: 40px;
  line-height: 40px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  transition: 0.3s color;
}
.v-picker-touch-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
}
.v-picker-item-selected {
  color: var(--textPrimary);
}
.v-picker-item-disabled {
  color: var(--lightGray);
}
</style>
